<template draggable="false" ondragstart="javascript: return false;">
  <div class="fe-toolbar-buttons">

    <div class="fe-toolbar-buttons-left">
      <div class="fe-toolbar-block">
        <!--撤销、重做-->
        <el-button-group>
          <el-button type="primary" size="mini" @click="undo" :disabled="!isUndoEnable" title="撤销" >
            <i class="iconfont fe-iconfont-undo"></i>
          </el-button>
          <el-button type="primary" size="mini" @click="redo" :disabled="!isRedoEnable" title="重做">
            <i class="iconfont fe-iconfont-redo"></i>
          </el-button>
        </el-button-group>
        <br/>
        <el-button-group>
          <el-button size="mini" @click="close" title="退出">
            <i class="iconfont fe-iconfont-cancel"></i>
          </el-button>
          <el-button type="primary" size="mini" @click="showSaveDialog" title="保存">
            <i class="iconfont fe-iconfont-save"></i>
          </el-button>
        </el-button-group>

      </div>
      <div class="fe-toolbar-block">

        <!--复制、剪切、粘贴-->
        <el-button-group>
          <el-button type="primary" size="mini" @click="copyCell" title="复制" >
            <i class="iconfont fe-iconfont-copy"></i>
          </el-button>
          <el-button type="primary" size="mini" @click="redo" title="剪切">
            <i class="iconfont fe-iconfont-cut"></i>
          </el-button>
        </el-button-group>
        <br/>
        <el-button-group>
          <el-button type="primary" size="mini" @click="redo" title="粘贴">
            <i class="iconfont fe-iconfont-paste"></i>
          </el-button>
          <el-button type="primary" size="mini" @click="redo" title="取消复制、粘贴">
            <i class="iconfont fe-iconfont-cancel"></i>
          </el-button>
        </el-button-group>
      </div>

      <div class="fe-toolbar-block">
        <el-button-group>
          <el-button type="primary" size="mini" @click="clearCellsContent" title="清空内容">
            <i class="iconfont fe-iconfont-clear"></i>
          </el-button>
          <el-button type="primary" size="mini" @click="setArea" title="工作区域，实际处理工作时，只显示工作区域内的内容">
            <i class="iconfont fe-iconfont-area"></i>
          </el-button>
        </el-button-group>
        <br/>
        <el-button-group>
          <el-button type="primary" size="mini" @click="clearDetail" title="取消设置明细控件">
            <i class="iconfont fe-iconfont-delete-detail"></i>
          </el-button>
          <el-button type="primary" size="mini" @click="setDetail" title="设置为明细组件">
            <i class="iconfont fe-iconfont-detail"></i>
          </el-button>
        </el-button-group>
      </div>

      <div class="fe-toolbar-block">
        <el-button-group>
          <swatches :value="backgroundColor" colors="text-advanced" @input="setCellsBackground">
            <el-button slot="trigger" type="primary" size="mini" title="背景色">
              <i class="iconfont fe-iconfont-background-color">
              </i>
            </el-button>
          </swatches>
          <swatches :value="textColor" colors="text-advanced" @input="setCellsTextColor">
            <el-button slot="trigger" type="primary" size="mini" title="背景色">
              <i class="iconfont fe-iconfont-text-color">
              </i>
            </el-button>
          </swatches>
        </el-button-group>
        <br/>

        <el-upload
          ref="upload"
          :on-change="readfile"
          :show-file-list="false"
          :auto-upload="false"
          action="//"
          >
          <!--<el-button-group>-->
            <el-button type="primary" size="mini" title="导出" @click="exportForm">
              <i class="iconfont fe-iconfont-export"></i>
            </el-button>
            <el-button slot="trigger" type="primary" size="mini" title="导入" @click="importForm">
              <i class="iconfont fe-iconfont-import"></i>
            </el-button>
          <!--</el-button-group>-->

        </el-upload>
      </div>

      <div class="fe-toolbar-block">
        <el-select size="mini" :value="fontfamily" @change="setCellsTextFontFamily" placeholder="字体" >
          <el-option
            v-for="item in fontFamilyItems"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>

        <el-select size="mini" :value="fontsize" @change="setCellsTextFontSize" placeholder="字号" >
          <el-option
            v-for="item in fontSizeItems"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
        <br/>
        <!--粗体、斜体、下划线-->
        <el-button-group>
          <el-button type="primary" size="mini" @click="setBold" title="粗体">
            <i class="iconfont fe-iconfont-bold"></i>
          </el-button>
          <el-button type="primary" size="mini" @click="setItalic" title="斜体">
            <i class="iconfont fe-iconfont-italic"></i>
          </el-button>
          <el-button type="primary" size="mini" @click="setTextDecoration" title="下划线">
            <i class="iconfont fe-iconfont-underline"></i>
          </el-button>
        </el-button-group>

        <!--合并、分割-->
        <el-button-group>
          <el-button type="primary" size="mini" @click="mergeCells" title="合并单元格">
            <i class="iconfont fe-iconfont-mergecell"></i>
          </el-button>
          <el-button type="primary" size="mini" @click="splitCells" title="取消合并">
            <i class="iconfont fe-iconfont-separatecell"></i>
          </el-button>
        </el-button-group>

        <el-button-group>
          <el-button type="primary" size="mini" @click="showBorderPropertyDialog">
            <i class="iconfont fe-iconfont-border-all"></i>
          </el-button>
        </el-button-group>
      </div>

      <div class="fe-toolbar-block">
        <el-button-group>
          <el-button type="primary" size="mini" @click="setCellsTextAlign({align: 'left'})" title="居左">
            <i class="iconfont fe-iconfont-alignleft"></i>
          </el-button>
          <el-button type="primary" size="mini" @click="setCellsTextAlign({align: 'center'})" title="居中">
            <i class="iconfont fe-iconfont-aligncenter"></i>
          </el-button>
          <el-button type="primary" size="mini" @click="setCellsTextAlign({align: 'right'})" title="居右">
            <i class="iconfont fe-iconfont-alignright"></i>
          </el-button>
        </el-button-group>
        <br/>
        <el-button-group>
          <el-button type="primary" size="mini" @click="setCellsTextValign({align: 'top'})" title="顶端对齐">
            <i class="iconfont fe-iconfont-aligntop"></i>
          </el-button>
          <el-button type="primary" size="mini" @click="setCellsTextValign({align: 'middle'})" title="垂直居中">
            <i class="iconfont fe-iconfont-alignmiddle"></i>
          </el-button>
          <el-button type="primary" size="mini" @click="setCellsTextValign({align: 'bottom'})" title="底部对齐">
            <i class="iconfont fe-iconfont-alignbottom"></i>
          </el-button>
        </el-button-group>
      </div>

      <div class="fe-toolbar-block">
        <el-button-group>
          <el-button type="primary" size="mini" @click="addControlConfirm('input')" title="文本框">
            <i class="iconfont fe-iconfont-input"></i>
          </el-button>
          <el-button type="primary" size="mini" @click="addControlConfirm('textarea')" title="文本域/多行文本">
            <i class="iconfont fe-iconfont-text"></i>
          </el-button>
          <el-button type="primary" size="mini" @click="addControlConfirm('date')" title="日期控件">
            <i class="iconfont fe-iconfont-date"></i>
          </el-button>
          <el-button type="primary" size="mini" @click="addControlConfirm('select')" title="下拉框">
            <i class="iconfont fe-iconfont-drop-down"></i>
          </el-button>
          <el-button type="primary" size="mini" @click="addControlConfirm('radio')" title="单选框">
            <i class="iconfont fe-iconfont-radio"></i>
          </el-button>
          <el-button type="primary" size="mini" @click="addControlConfirm('checkbox')" title="多选框">
            <i class="iconfont fe-iconfont-checkbox"></i>
          </el-button>
        </el-button-group>
        <br/>
        <el-button-group>
          <el-button type="primary" size="mini" @click="addControlConfirm('numeric')" title="数字控件">
            <i class="iconfont fe-iconfont-numeric"></i>
          </el-button>
          <el-button type="primary" size="mini" @click="addControlConfirm('refer')" title="参照窗口">
            <i class="iconfont fe-iconfont-refer-window"></i>
          </el-button>
          <el-button type="primary" size="mini" @click="addControlConfirm('sequence')" title="序列号，仅限添加在明细控件里">
            <i class="iconfont fe-iconfont-number"></i>
          </el-button>
        </el-button-group>
      </div>
    </div>
    <!--<div class="fe-toolbar-buttons-right">-->
      <!--<div class="fe-toolbar-block2">-->
        <!--<el-button-group>-->
          <!--<el-button type="danger" size="mini" @click="redo" title="取消">-->
            <!--<i class="iconfont fe-iconfont-cancel"></i>-->
          <!--</el-button>-->
          <!--<el-button type="primary" size="mini" @click="showSaveDialog" title="保存">-->
            <!--<i class="iconfont fe-iconfont-save"></i>-->
          <!--</el-button>-->
        <!--</el-button-group>-->
      <!--</div>-->
    <!--</div>-->

    <el-dialog :visible.sync="borderPropertyDialog" width="550px" custom-class="">
      <div slot="title" class="fe-toolbar-buttons-border-property-dialog-title">
        边框属性
      </div>
      <fieldset class="fe-toolbar-buttons-border-property-dialog-fieldset" style="float: left; width: 200px;">
        <legend>线条</legend>
        <el-select v-model="borderStyle" placeholder="请选择" size="mini">
          <el-option
            v-for="item in borderPropertyStyleOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </fieldset>
      <fieldset class="fe-toolbar-buttons-border-property-dialog-fieldset">
        <legend>预置</legend>
        <el-button type="primary" size="mini" @click="setBorderNone">
          <i class="iconfont fe-iconfont-border-none">
            <span class="fe-toolbar-buttons-border-property-dialog-font-mini">无边框</span>
          </i>
        </el-button>
        <el-button type="primary" size="mini" @click="setBorderOuter">
          <i class="iconfont fe-iconfont-border-outer">
            <span class="fe-toolbar-buttons-border-property-dialog-font-mini">外边框</span>
          </i>
        </el-button>
        <el-button type="primary" size="mini" @click="setBorderInner">
          <i class="iconfont fe-iconfont-border-inner">
            <span class="fe-toolbar-buttons-border-property-dialog-font-mini">内边框</span>
          </i>
        </el-button>
      </fieldset>

      <fieldset class="fe-toolbar-buttons-border-property-dialog-fieldset" style="float: left; width: 200px;">
        <legend>颜色</legend>
        <!--<el-color-picker v-model="borderColor" :predefine="predefinedColors" size="small"></el-color-picker>-->

        <swatches v-model="borderColor" colors="text-advanced" swatch-size="28">
          <!--<el-button slot="trigger" type="primary" size="mini" title="背景色">-->
            <!--<i class="iconfont fe-iconfont-background-color">-->
            <!--</i>-->
          <!--</el-button>-->
        </swatches>

        <!--<el-radio-group v-model="borderColor" size="small">-->
          <!--<el-radio-button label="#000000">黑色</el-radio-button>-->
          <!--<el-radio-button label="#FF0000">红色</el-radio-button>-->
        <!--</el-radio-group>-->
      </fieldset>

      <fieldset class="fe-toolbar-buttons-border-property-dialog-fieldset">
        <legend>自定义</legend>
        <table class="fe-toolbar-buttons-border-property-dialog-table-border-buttons">
          <tr>
            <td>
              <el-button type="primary" size="mini" @click="setBorderTop">
                <i class="iconfont fe-iconfont-border-top"></i>
              </el-button>
            </td>
            <td colspan="3" rowspan="3">
              <!--<table cellspacing="0" cellpadding="0" class="fe-toolbar-preview fe-toolbar-preview-top fe-toolbar-preview-horizon fe-toolbar-preview-bottom fe-toolbar-preview-left fe-toolbar-preview-vertical fe-toolbar-preview-right"-->
                <!--v-bind:class="{-->
                    <!--feToolbarPreviewTop: borderPosition.top-->
                <!--}"-->
              <!--&gt;-->
              <table cellspacing="0" cellpadding="0" class="fe-toolbar-preview"
                     v-bind:class="{
                     }"
                     :style="{
                      borderTopColor: borderTop.color,
                      borderBottomColor: borderBottom.color,
                      borderLeftColor: borderLeft.color,
                      borderRightColor: borderRight.color,
                      borderTopStyle: borderTop.style,
                      borderBottomStyle: borderBottom.style,
                      borderLeftStyle: borderLeft.style,
                      borderRightStyle: borderRight.style,
                      borderTopWidth: borderTop.width,
                      borderBottomWidth: borderBottom.width,
                      borderLeftWidth: borderLeft.width,
                      borderRightWidth: borderRight.width,
                     }"
              >
                <tr>
                  <td :style="{
                      borderRightColor: borderVertical.color,
                      borderBottomColor: borderHorizon.color,
                      borderRightStyle: borderVertical.style,
                      borderBottomStyle: borderHorizon.style,
                      borderRightWidth: borderVertical.width,
                      borderBottomWidth: borderHorizon.width,
                     }"
                  ></td>
                  <td :style="{
                      borderBottomColor: borderHorizon.color,
                      borderBottomStyle: borderHorizon.style,
                      borderBottomWidth: borderHorizon.width
                     }"
                  ></td>
                </tr>
                <tr>
                  <td :style="{
                      borderRightColor: borderVertical.color,
                      borderRightStyle: borderVertical.style,
                      borderRightWidth: borderVertical.width
                     }"
                  ></td>
                  <td></td>
                </tr>
              </table>
            </td>
          </tr>
          <tr>
            <td>
              <el-button type="primary" size="mini" @click="setBorderHorizon">
                <i class="iconfont fe-iconfont-border-horizontal"></i>
              </el-button>
            </td>
          </tr>
          <tr>
            <td>
              <el-button type="primary" size="mini" @click="setBorderBottom">
                <i class="iconfont fe-iconfont-borderbottom"></i>
              </el-button>
            </td>
          </tr>
          <tr>
            <td></td>
            <td style="width: 50px; text-align: left">
              <el-button type="primary" size="mini" @click="setBorderLeft">
                <i class="iconfont fe-iconfont-borderleft"></i>
              </el-button>
            </td>
            <td style="width: 50px; text-align: center">
              <el-button type="primary" size="mini" @click="setBorderVertical">
                <i class="iconfont fe-iconfont-border-vertical"></i>
              </el-button>
            </td>
            <td style="width: 50px; text-align: right">
              <el-button type="primary" size="mini" @click="setBorderRight">
                <i class="iconfont fe-iconfont-border-right"></i>
              </el-button>
            </td>
          </tr>
        </table>
        <br/>
      </fieldset>
      <div slot="footer" class="dialog-footer">
        <el-button size="mini" @click="borderPropertyDialog = false">取 消</el-button>
        <el-button size="mini" type="primary" @click="setCellsBorder({borderTop, borderHorizon, borderBottom, borderLeft, borderVertical, borderRight}); borderPropertyDialog = false;">确 定</el-button>
      </div>
    </el-dialog>

    <!--input-->
    <el-dialog :visible.sync="editControl.type === 'input'" :width="controlDialog.dialogWidth" @open="setDialogForm" @close="closeDialog">
      <div slot="title" class="fe-dialog-title">
        文本框属性
      </div>
      <el-form :model="controlDialog.inputAttribute" ref="inputForm" :rules="controlDialog.inputRules" label-width="80px" size="mini">
        <el-form-item label="控件名称" prop="name">
          <el-input v-model.trim="controlDialog.inputAttribute.name" :autofocus="true" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="控件宽度" prop="width">
          <el-input v-model.trim.number="controlDialog.inputAttribute.width" :min="50" :max="999" placeholder="最小50，空表示和单元格等宽" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="closeDialog" size="mini">取 消</el-button>
        <el-button type="primary" @click="addInputSave" size="mini">确 定</el-button>
      </div>
    </el-dialog>

    <!--textarea-->
    <el-dialog :visible.sync="editControl.type === 'textarea'" :width="controlDialog.dialogWidth" @open="setDialogForm" @close="closeDialog">
      <div slot="title" class="fe-dialog-title">
        文本域属性
      </div>
      <el-form :model="controlDialog.textareaAttribute" ref="textareaForm" :rules="controlDialog.textareaRules" label-width="80px" size="mini">
        <el-form-item label="控件名称" prop="name">
          <el-input v-model.trim="controlDialog.textareaAttribute.name" :autofocus="true" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="控件宽度" prop="width">
          <el-input v-model.trim.number="controlDialog.textareaAttribute.width" :min="50" :max="999" placeholder="最小50，空表示和单元格等宽" autocomplete="off"></el-input>
        </el-form-item>
        <!--<el-form-item label="控件宽度" prop="height">-->
          <!--<el-input v-model.trim.number="controlDialog.textareaAttribute.height" :min="50" :max="999" placeholder="最小50，空表示和单元格等高" autocomplete="off"></el-input>-->
        <!--</el-form-item>-->
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="closeDialog" size="mini">取 消</el-button>
        <el-button type="primary" @click="addTextareaSave" size="mini">确 定</el-button>
      </div>
    </el-dialog>

    <!--date-->
    <el-dialog :visible.sync="editControl.type === 'date'" :width="controlDialog.dialogWidth" @open="setDialogForm" @close="closeDialog">
      <div slot="title" class="fe-dialog-title">
        日期控件属性
      </div>
      <el-form :model="controlDialog.dateAttribute" ref="dateForm" :rules="controlDialog.dateRules" label-width="80px" size="mini">
        <el-form-item label="控件名称" prop="name">
          <el-input v-model.trim="controlDialog.dateAttribute.name" :autofocus="true" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="控件宽度" prop="width">
          <el-input v-model.trim.number="controlDialog.dateAttribute.width" :min="0" :max="999" placeholder="最小60，空表示和单元格等宽" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="日期格式" prop="height">

          <el-select v-model="controlDialog.dateAttribute.format" placeholder="请选择日期格式" style="width: 100% !important;">
            <el-option key="yyyy-MM-dd" label="yyyy-MM-dd" value="yyyy-MM-dd"/>
            <el-option key="yyyy年MM月dd日" label="yyyy年MM月dd日" value="yyyy年MM月dd日"/>
          </el-select>

        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="closeDialog" size="mini">取 消</el-button>
        <el-button type="primary" @click="addDateSave" size="mini">确 定</el-button>
      </div>
    </el-dialog>

    <!--select-->
    <el-dialog :visible.sync="editControl.type === 'select'" :width="controlDialog.dialogWidth2" @open="setDialogForm" @close="closeDialog">
      <div slot="title" class="fe-dialog-title">
        下拉框属性
      </div>
      <el-form :model="controlDialog.selectAttribute" ref="selectForm" :rules="controlDialog.selectRules" label-width="80px" size="mini">
        <el-row type="flex" class="row-bg">
          <el-col :span="12">
            <el-form-item label="控件名称" prop="name">
              <el-input v-model.trim="controlDialog.selectAttribute.name" :autofocus="true" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="控件宽度" prop="width">
              <el-input v-model.trim.number="controlDialog.selectAttribute.width" :min="0" :max="999" placeholder="最小50，空表示和单元格等宽" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-table
              :data="controlDialog.selectAttribute.options"
              height="280"
              border
              style="width: 100%">
              <el-table-column
                prop="value"
                label="值"
                width="150">
                <template slot-scope="scope">
                  <el-input v-model.trim="scope.row.value" placeholder="请输入值"></el-input>
                </template>
              </el-table-column>
              <el-table-column
                prop="label"
                label="显示文本"
                width="180">
                <template slot-scope="scope">
                  <el-input v-model.trim="scope.row.text" placeholder="请输入显示文本"></el-input>
                </template>
              </el-table-column>
              <el-table-column
                label="操作"
                width="120">
                <template slot-scope="scope">
                  <el-button-group>
                    <el-button type="primary" size="mini" icon="el-icon-delete" @click="controlDialog.selectAttribute.options.splice(scope.$index, 1)"></el-button>
                    <el-button type="primary" size="mini" icon="el-icon-arrow-up" v-if="scope.$index !== 0" @click="controlDialog.selectAttribute.options.splice(scope.$index - 1, 0, controlDialog.selectAttribute.options.splice(scope.$index, 1)[0])"></el-button>
                    <el-button type="primary" size="mini" icon="el-icon-arrow-down" v-if="scope.$index !== controlDialog.selectAttribute.options.length - 1" @click="controlDialog.selectAttribute.options.splice(scope.$index + 1, 0, controlDialog.selectAttribute.options.splice(scope.$index, 1)[0])"></el-button>
                  </el-button-group>
                  <!--<el-button type="text" size="mini" @click="controlDialog.selectAttribute.options.splice(scope.$index, 1)">移除</el-button>-->
                  <!--<el-button type="text" size="mini" v-if="scope.$index !== 0" @click="controlDialog.selectAttribute.options.splice(scope.$index - 1, 0, controlDialog.selectAttribute.options.splice(scope.$index, 1)[0])">上移</el-button>-->
                  <!--<el-button type="text" size="mini" v-if="scope.$index !== controlDialog.selectAttribute.options.length - 1" @click="controlDialog.selectAttribute.options.splice(scope.$index + 1, 0, controlDialog.selectAttribute.options.splice(scope.$index, 1)[0])">下移</el-button>-->
                </template>
              </el-table-column>
              <div slot="empty">
                <div>
                  暂无数据...
                </div>
                <div>
                  <el-button type="primary" size="mini" icon="el-icon-plus" @click="controlDialog.selectAttribute.options.push({})"></el-button>
                </div>
              </div>
              <div slot="append" style="text-align: center; padding: 5px;">
                <el-button type="primary" size="mini" icon="el-icon-plus" @click="controlDialog.selectAttribute.options.push({})"></el-button>
              </div>
            </el-table>
          </el-col>
        </el-row>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="closeDialog" size="mini">取 消</el-button>
        <el-button type="primary" @click="addSelectSave" size="mini">确 定</el-button>
      </div>
    </el-dialog>

    <!--checkbox-->
    <el-dialog :visible.sync="editControl.type === 'checkbox'" :width="controlDialog.dialogWidth2" @open="setDialogForm" @close="closeDialog">
      <div slot="title" class="fe-dialog-title">
