<script>
import { mapActions } from 'vuex'
import { Message, MessageBox } from 'element-ui'
import { getLocalStorageItem, setLocalStorageItem } from '../util'
import { CONST_LOCALSTORATE_STATELIST_KEY, CONST_LOCALSTORATE_STATEINDEX_KEY } from '../const'
import { http } from '../util/http'
import { Base64 } from 'js-base64'
import Swatches from 'vue-swatches'

// Import the styles too, globally
import 'vue-swatches/dist/vue-swatches.min.css'
export default {
  name: 'Toolbar',
  components: {
    Swatches
  },
  computed: {
    area () {
      return this.$store.state.area
    },
    detail () {
      return this.$store.state.detail
    },
    form () {
      return this.$store.state.form
    },
    cells () {
      return this.$store.state.cells
    },
    formTypeList () {
      return this.$store.state.formTypeList
    },
    type () {
      return this.$store.state.cache.type
    },
    editControl () {
      return this.$store.state.cache.editControl
    },
    fontFamilyItems () {
      return this.$store.state.toolbarConfig.fontFamilyItems
    },
    fontSizeItems () {
      return this.$store.state.toolbarConfig.fontSizeItems
    },
    predefinedColors () {
      return this.$store.state.config.predefinedColors
    },
    backList () {
      return this.$store.state.backList
    },
    nextList () {
      return this.$store.state.nextList
    },
    isUndoEnable () {
      // let stateList = JSON.parse(getLocalStorageItem(CONST_LOCALSTORATE_STATELIST_KEY) || '[]')
      let stateIndex = this.$store.state.stateIndex
      return stateIndex > 0
    },
    isRedoEnable () {
      let stateList = JSON.parse(getLocalStorageItem(CONST_LOCALSTORATE_STATELIST_KEY) || '[]')
      let stateIndex = this.$store.state.stateIndex
      return stateIndex < stateList.length - 1 && stateIndex >= 0
    },
    fontsize () {
      let beginRow = this.$store.state.cache.select.beginCell.row
      let beginCol = this.$store.state.cache.select.beginCell.col

      if (beginRow && beginCol && this.$store.state.cells[beginRow][beginCol]) {
        return this.$store.state.cells[beginRow][beginCol].fontSize
      }

      return ''
    },
    fontfamily () {
      let beginRow = this.$store.state.cache.select.beginCell.row
      let beginCol = this.$store.state.cache.select.beginCell.col

      if (beginRow && beginCol && this.$store.state.cells[beginRow][beginCol]) {
        return this.$store.state.cells[beginRow][beginCol].fontFamily
      }

      return ''
    },
    backgroundColor () {
      let beginRow = this.$store.state.cache.select.beginCell.row
      let beginCol = this.$store.state.cache.select.beginCell.col

      if (beginRow && beginCol && this.$store.state.cells[beginRow][beginCol]) {
        return this.$store.state.cells[beginRow][beginCol].background
      }

      return ''
    },
    textColor () {
      let beginRow = this.$store.state.cache.select.beginCell.row
      let beginCol = this.$store.state.cache.select.beginCell.col

      if (beginRow && beginCol && this.$store.state.cells[beginRow][beginCol]) {
        return this.$store.state.cells[beginRow][beginCol].color
      }

      return ''
    },
    items () {
      let items = []
      let cells = this.$store.state.cells
      let currentRow = this.$store.state.cache.select.beginCell.row
      let currentCol = this.$store.state.cache.select.beginCell.col
      for (let i in cells) {
        for (let j in cells[i]) {
          let cell = cells[i][j]
          if (cell && cell.attribute && cell.attribute.name && (currentRow !== (i - 0) || currentCol !== (j - 0))) {
            items.push({...cell.attribute, type: cell.type})
          }
        }
      }

      items = items.filter(item => item.type === 'input' || item.type === 'textarea' || item.type === 'numeric')

      // 按名称排序
      items.sort((o1, o2) => {
        return (o1.name || '').localeCompare(o2.name || '')
      })

      return items
    }
  },
