  methods: {
    addNumeric (d) {
      let preExpressionItem = this.controlDialog.numericAttribute.expressionItems.pop()
      if (preExpressionItem === undefined) {
        this.controlDialog.numericAttribute.expressionItems.push(d)
        this.controlDialog.numericAttribute.relateItems.push('')
        this.controlDialog.numericAttribute.displayItems.push({type: 'numeric', text: d})
      } else if (/^[0-9]+(.[0-9]*)?$/.test(preExpressionItem)) {
        if (preExpressionItem.indexOf('.') >= 0 && d === '.') {
          this.controlDialog.numericAttribute.expressionItems.push(preExpressionItem)
          return true
        }

        preExpressionItem = preExpressionItem + d
        this.controlDialog.numericAttribute.expressionItems.push(preExpressionItem)

        this.controlDialog.numericAttribute.relateItems.push('')

        let preDisplayItem = this.controlDialog.numericAttribute.displayItems.pop()
        preDisplayItem.text = preDisplayItem.text + d
        this.controlDialog.numericAttribute.displayItems.push(preDisplayItem)
      } else {
        this.controlDialog.numericAttribute.expressionItems.push(preExpressionItem)
        this.controlDialog.numericAttribute.expressionItems.push(d)
        this.controlDialog.numericAttribute.relateItems.push('')
        this.controlDialog.numericAttribute.displayItems.push({type: 'numeric', text: d})
      }
    },
    addDot () {

    },
    addOperator (o) {
      if (this.controlDialog.numericAttribute.expressionItems.length === 0) {
        this.controlDialog.numericAttribute.expressionItems.push(o)
        this.controlDialog.numericAttribute.relateItems.push('')
        this.controlDialog.numericAttribute.displayItems.push({type: 'operate', text: o})
        return true
      }

      let preDisplayItem = this.controlDialog.numericAttribute.displayItems[this.controlDialog.numericAttribute.displayItems.length - 1]
      if (preDisplayItem && preDisplayItem.type === 'operate') {
        return true
      }

      if (preDisplayItem) {
        this.controlDialog.numericAttribute.expressionItems.push(o)
        this.controlDialog.numericAttribute.relateItems.push('')
        this.controlDialog.numericAttribute.displayItems.push({type: 'operate', text: o})
      }
    },
    addBracket (b) {
      this.controlDialog.numericAttribute.expressionItems.push(b)
      this.controlDialog.numericAttribute.relateItems.push('')
      this.controlDialog.numericAttribute.displayItems.push({type: 'bracket', text: b})
    },
    addItem () {
      let targetItemid = this.controlDialog.numericAttribute.numericItem
      for (let i in this.controlDialog.numericItems) {
        let numericItem = this.controlDialog.numericItems[i]
        if (targetItemid === numericItem.itemid) {
          this.controlDialog.numericAttribute.expressionItems.push('{' + numericItem.name + '}')
          this.controlDialog.numericAttribute.relateItems.push(targetItemid)
          this.controlDialog.numericAttribute.displayItems.push({type: 'item', text: '{' + numericItem.name + '}', itemid: numericItem.itemid})

          break
        }
      }
    },
    backspace () {
      if (this.controlDialog.numericAttribute.displayItems.length === 0) {
        return true
      }

      let preDisplayItem = this.controlDialog.numericAttribute.displayItems[this.controlDialog.numericAttribute.displayItems.length - 1]
      if (preDisplayItem && preDisplayItem.type === 'numeric') {
        if (preDisplayItem.text.length === 1) {
          this.controlDialog.numericAttribute.expressionItems.pop()
          this.controlDialog.numericAttribute.relateItems.pop()
          this.controlDialog.numericAttribute.displayItems.pop()
          return true
        }

        let preExpressionItem = this.controlDialog.numericAttribute.expressionItems[this.controlDialog.numericAttribute.expressionItems.length - 1]
        preExpressionItem = preExpressionItem.substr(0, preExpressionItem.length - 1)
        this.controlDialog.numericAttribute.expressionItems[this.controlDialog.numericAttribute.expressionItems.length - 1] = preExpressionItem

        preDisplayItem.text = preDisplayItem.text.substr(0, preDisplayItem.text.length - 1)
        return true
      }

      this.controlDialog.numericAttribute.expressionItems.pop()
      this.controlDialog.numericAttribute.relateItems.pop()
      this.controlDialog.numericAttribute.displayItems.pop()
    },
    clear () {
      this.controlDialog.numericAttribute.expressionItems = []
      this.controlDialog.numericAttribute.relateItems = []
      this.controlDialog.numericAttribute.displayItems = []
    },
    validateName (rule, value, callback) {
      if (value === '') {
        callback(new Error('请输入项目名称'))
      }

      const reservedWords = ['detail', '明细']
      let exists = reservedWords.some(word => word === value)
      if (exists) {
        callback(new Error('名称[' + value + ']为保留字'))
      }

      let conficted = false
      for (let i = 1; i < this.cells.length && !conficted; i++) {
        for (let j = 1; j < this.cells[i].length && !conficted; j++) {
          let cell = this.cells[i][j]
          if (cell && cell.attribute && value === cell.attribute.name && (this.editControl.row !== i || this.editControl.col !== j)) {
            conficted = true
            break
          }
        }
      }

      if (conficted) {
        callback(new Error('名称重复'))
      }

      let flag = this.nameExpression.test(value)
      if (!flag) {
        callback(new Error('请输入合法字符：中文、字母、数字、括号、下划线'))
      } else {
        callback()
      }
    },
    validateInputWidth (rule, value, callback) {
      if (value !== '') {
        if (!/^\d+$/gi.test(value)) {
          callback(new Error('请输入整数'))
        }

        value = value - 0
        if (value < 50 || value > 999) {
          callback(new Error('请输入50-999之间的数字'))
        } else {
          callback()
        }
      } else {
        callback()
      }
    },
    validateTextWidth (rule, value, callback) {
      if (value !== '') {
        if (!/^\d+$/gi.test(value)) {
