    setDialogForm () {
      // console.log('setDialogForm')
      if (this.editControl.type === 'input') {
        this.controlDialog.inputAttribute.name = this.editControl.attribute.name
        this.controlDialog.inputAttribute.width = this.editControl.attribute.width
      } else if (this.editControl.type === 'textarea') {
        this.controlDialog.textareaAttribute.name = this.editControl.attribute.name
        this.controlDialog.textareaAttribute.width = this.editControl.attribute.width
        this.controlDialog.textareaAttribute.height = this.editControl.attribute.height
      } else if (this.editControl.type === 'date') {
        this.controlDialog.dateAttribute.name = this.editControl.attribute.name
        this.controlDialog.dateAttribute.width = this.editControl.attribute.width
        this.controlDialog.dateAttribute.format = this.editControl.attribute.format
      } else if (this.editControl.type === 'select') {
        this.controlDialog.selectAttribute.name = this.editControl.attribute.name
        this.controlDialog.selectAttribute.width = this.editControl.attribute.width
        this.controlDialog.selectAttribute.options = this.editControl.attribute.options
      } else if (this.editControl.type === 'checkbox') {
        this.controlDialog.checkboxAttribute.name = this.editControl.attribute.name
        this.controlDialog.checkboxAttribute.width = this.editControl.attribute.width
        this.controlDialog.checkboxAttribute.options = this.editControl.attribute.options
      } else if (this.editControl.type === 'radio') {
        this.controlDialog.radioAttribute.name = this.editControl.attribute.name
        this.controlDialog.radioAttribute.width = this.editControl.attribute.width
        this.controlDialog.radioAttribute.options = this.editControl.attribute.options
      } else if (this.editControl.type === 'numeric') {
        this.controlDialog.numericAttribute.name = this.editControl.attribute.name
        this.controlDialog.numericAttribute.width = this.editControl.attribute.width
        this.controlDialog.numericAttribute.precision = this.editControl.attribute.precision
        this.controlDialog.numericAttribute.numericItem = this.editControl.attribute.numericItem
        this.controlDialog.numericAttribute.expressionItems = this.editControl.attribute.expressionItems || []
        this.controlDialog.numericAttribute.relateItems = this.editControl.attribute.relateItems || []
        this.controlDialog.numericAttribute.displayItems = this.editControl.attribute.displayItems || []

        this.controlDialog.numericItems = []
        for (let i in this.cells) {
          for (let j in this.cells[i]) {
            let cell = this.cells[i][j]
            if (cell && cell.type === 'numeric' && cell.attribute.name !== this.editControl.attribute.name) {
              this.controlDialog.numericItems.push(cell.attribute)
            }
          }
        }
      } else if (this.editControl.type === 'sequence') {
        this.controlDialog.sequenceAttribute.name = this.editControl.attribute.name
      } else if (this.editControl.type === 'refer') {
        this.controlDialog.referAttribute.name = this.editControl.attribute.name
        this.controlDialog.referAttribute.width = this.editControl.attribute.width
        this.controlDialog.referAttribute.referCode = this.editControl.attribute.referCode
        this.controlDialog.referAttribute.maps = this.editControl.attribute.maps
        // this.controlDialog.referColumnList = []
        // this.controlDialog.referInfo.code = ''
        // this.controlDialog.referInfo.name = ''
        this.resetReferInfo()

        this.resolveRefer(this.editControl.attribute.referCode, this.editControl.attribute.maps)
      }
    },
    showSaveDialog () {
      if (!this.area.beginCell.row || !this.area.beginCell.col || !this.area.endCell.row || !this.area.endCell.col) {
        Message.warning('请设置工作区域')
        return false
      }

      if (this.detail.beginCell.row !== undefined) {
        if (this.detail.beginCell.col !== this.area.beginCell.col || this.detail.endCell.col !== this.area.endCell.col) {
          Message.warning('明细控件宽度应与工作区域等宽')
          return false
        }
      }

      this.saveDialog.visible = true
      this.saveDialog.saveForm.formKey = this.$store.state.form.formKey
      this.saveDialog.saveForm.name = this.$store.state.form.name
      this.saveDialog.saveForm.remark = this.$store.state.form.remark
      this.saveDialog.saveForm.formType = this.$store.state.form.formType
      // this.save(this.saveDialog.saveForm)
    },
    resetReferInfo () {
      this.controlDialog.referColumnList = []
      this.controlDialog.referInfo.code = ''
      this.controlDialog.referInfo.name = ''
    },
    resolveRefer (referCode, maps) {
      this.resetReferInfo()

      if (referCode) {
        let loading = this.$loading({
          fullscreen: true,
          text: '正在请求资源...'
        })
        let _this_ = this

        http.get('api/field/searchReferWindow?referCode=' + referCode)
          .then(function (response) {
            // console.log(response)
            loading.close()
            // response = data

            if (response.data === null) {
              _this_.resetReferInfo()
              _this_.controlDialog.referAttribute.referCode = ''
              _this_.$message.error('参照窗口输入错误')
              return
            }

            // TODO
            _this_.controlDialog.referInfo.code = response.data.code
            _this_.controlDialog.referInfo.name = response.data.name
            _this_.controlDialog.referColumnList = response.data.columnList
            if (!maps) {
              _this_.controlDialog.referAttribute.maps = []
              response.data.columnList.forEach(() => _this_.controlDialog.referAttribute.maps.push({from: '', to: ''}))
            }
          })
          .catch(function (error) {
            console.log(error)
            loading.close()
            // _this_.controlDialog.referAttribute.referCode = ''
          })
        // console.log(data)

        // TODO
        // this.controlDialog.referInfo.code = data.data.code
        // this.controlDialog.referInfo.name = data.data.name
        // this.controlDialog.referColumnList = data.data.columnList
        // if (!maps) {
        //   this.controlDialog.referAttribute.maps = []
        //   data.data.columnList.forEach(() => this.controlDialog.referAttribute.maps.push({from: '', to: ''}))
        // }
        //
        // console.log(this.controlDialog.referAttribute.maps)
        // console.log(this.items)
      }
    },
    saveForm () {
      this.save(this.saveDialog.saveForm)
      this.saveDialog.visible = false
    },
    close () {
      this.$confirm('是否关闭当前窗口', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        console.log('yes')
        window.close()
      }).catch(() => {
        console.log('no')
      })
    },
    exportForm () {
      this.$prompt('请输入文件名', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消'
      }).then((param) => {
        let json = {
          cells: this.$store.state.cells,
          cols: this.$store.state.cols,
          rows: this.$store.state.rows,
          rowNum: this.$store.state.rowNum,
          colNum: this.$store.state.colNum,
          area: this.$store.state.area,
          detail: this.$store.state.detail
        }

        console.log(json)
        console.log(Base64)

        let urlObject = window.URL || window.webkitURL || window
        let text = Base64.encode(JSON.stringify(this.$store.state))
        let exportBlob = new Blob([text])

        let saveLink = document.createElementNS('http://www.w3.org/1999/xhtml', 'a')
        saveLink.href = urlObject.createObjectURL(exportBlob)
        saveLink.download = param.value + '.fields'

        let ev = document.createEvent('MouseEvents')
        ev.initMouseEvent(
          'click', true, false, window, 0, 0, 0, 0, 0
          , false, false, false, false, 0, null
        )
        saveLink.dispatchEvent(ev)
      }).catch((e) => {
        console.log(e)
        // this.$message({
        //   type: 'info',
        //   message: '取消输入'
        // })
      })
    },
    importForm () {

    },
    readfile (file, fileList) {
      let _this_ = this
      var reader = new FileReader()
      reader.readAsText(file.raw)
      reader.onload = function () {
        try {
          var text = Base64.decode(reader.result)
          _this_.$store.replaceState(Object.assign({}, _this_.$store.state, JSON.parse(text)))
        } catch (e) {
          _this_.$message.error('导入出错')
        }
      }
      reader.onerror = function (e) {
        console.log('Error', e)
        _this_.$message.error('导入出错')
      }
    },
    ...mapActions([
      'beginSelectCell',
      'endSelectCell',
      'mergeSelectedCells',
      'mergeCells',
      'splitCells',
      'setCellsTextAlign',
      'setCellsTextValign',
      'setCellsBackground',
      'setCellsTextColor',
      'setCellsBorder',
      'beginUEdit',
      'doClearCellsContent',
      'doUndo',
      'doRedo',
      'copyCell',
      'setBold',
      'setTextDecoration',
      'setItalic',
      'setCellsTextFontSize',
      'setCellsTextFontFamily',
      'addControl',
      'beginAddControl',
      'cancelAddControl',
      'save',
      'setArea',
      'setDetail',
      'clearDetail'
    ])
  },
  mounted: function () {
    // console.log('toolbar')
  }
}
</script>

<style scoped id="element-ui-style">
  .fe-toolbar-buttons .el-select--mini {
    width: 102px !important;
  }
</style>

<style scoped>
  .fe-toolbar-preview tr td {
    width: 75px;
    height: 50px;
  }
  .fe-toolbar-preview-top-width {
    border-top-width: 1px;
  }
  .fe-toolbar-preview-horizon-width tr:first-child td {
    border-bottom-width: 1px;
  }
  .fe-toolbar-preview-bottom-width {
    border-bottom-width: 1px;
  }
  .fe-toolbar-preview-left-width {
    border-left-width: 1px;
  }
  .fe-toolbar-preview-vertical-width tr td:first-child {
    border-right-width: 1px;
  }
  .fe-toolbar-preview-right-width {
    border-right-width: 1px;
  }
  .fe-toolbar-buttons {
    font-size: 12px;
    background: #c8d8ed;
  }
  .fe-toolbar-buttons .fe-toolbar-buttons-left {
    float: left;
  }
  .fe-toolbar-buttons .fe-toolbar-buttons-right {
    text-align: right;
  }
  .fe-toolbar-buttons-border-property-dialog-font-mini {
    font-size: 12px;
  }
  .el-button--mini, .el-button--mini.is-round {
    padding: 5px 8px;
  }
  .fe-toolbar-buttons-border-property-dialog-fieldset {
    border: solid 1px #ddd;
  }
  .fe-toolbar-buttons-border-property-dialog-title {
    font-size: 14px;
  }
  .fe-toolbar-block {
    float: left;
    padding: 0px 2px 4px 2px;
    height: 60px;
    line-height: 30px;
  }
  .fe-toolbar-block2 {
    padding: 4px 2px 4px 2px;
    height: 60px;
    text-align: right;
  }
  .vue-swatches {
    position: relative;
    outline: none;
    float: left;
  }
  .fe-dialog-title {
    font-size: 16px;
  }

  .whole-size {
    width: 99% !important;
    margin: 2px;
  }
  /*.el-col {*/
    /*height: 28px;*/
    /*line-height: 28px;*/
  /*}*/
</style>



