<template draggable="false" ondragstart="javascript: return false;">
  <div draggable="false" ondragstart="javascript: return false;" v-loading="contentLoading" element-loading-text="拼命加载中">
    <FormEditor @loading="switchLoading"/>
    <ContentMenu/>
  </div>
</template>

<script>
import { http } from './util/http'
import axios from 'axios'
import { formKey, formId } from './util/param'

import FormEditor from './components/FormEditor'
import ContentMenu from './components/ContentMenu'
import NEditor from './components/NEditor'
import { mapActions } from 'vuex'

export default {
  name: 'app',
  components: {
    FormEditor,
    ContentMenu,
    NEditor
  },
  computed: {
    type () {
      return this.$store.state.cache.type
    },
    ueditorConfig () {
      return this.$store.state.config.ueditorConfig
    }
  },
  data: function () {
    return {
      contentLoading: false
    }
  },
  methods: {
    saveUEContent () {
      let source = this.$refs['ueditor'].getUEContent()
      this.endUEdit({source})
    },
    switchLoading () {
      this.contentLoading = !this.contentLoading
    },
    getForm (refreshToken) {
      // let windowName = window.name
      // let formKey = windowName.split('|')[1]
      // let formId = windowName.split('|')[2]

      let _this = this

      let loading = this.$loading({
        fullscreen: true,
        text: '正在请求资源...'
      })

      http.post('api/field/form/getForm', {id: formId, formKey: formKey})
        .then(function (response) {
          loading.close()
          let form = response.data
          let json = JSON.parse(form.json)
          json.form = form

          _this.$store.replaceState(Object.assign({}, _this.$store.state, json))
        })
        .catch(function (error) {
          console.log(error)
          loading.close()
        })
    },
    getFormType (refreshToken) {
      // console.log('getFormType')
      let _this = this

      http.post('api/field/formType/list', {refreshToken})
        .then(function (response) {
          let formTypeList = response.data.list
          _this.$store.replaceState(Object.assign({}, _this.$store.state, {formTypeList}))
        })
        .catch(function (error) {
          console.log(error)
        })
    },
    async (refreshToken) {
      return new Promise((resolve, reject) => {
        // setTimeout(() => {
        //   reject('请求失败')
        // }, 2000)
        this.getForm(refreshToken)
      })()
    },
    ...mapActions([
      'recordInitState',
      'setCacheType',
      'setForm'
    ])
  },
  mounted: function () {
    this.recordInitState()

    // let flag = true
    if (process.env.NODE_ENV === 'production') {
      let windowName = window.name
      let refreshToken = windowName.split('|')[0]
      axios.all([this.getFormType(refreshToken), this.getForm(refreshToken)])
        .then(axios.spread(function (acct, perms) {
        }))

      // this.async(refreshToken).then(this.getFormType)
    } else {
    }
  },
  beforeMount: function () {
  },
  beforeUpdate: function () {
    // if (this.type === 'undo' || this.type === 'redo') {
    //   this.loading = true
    // }
  },
  updated: function () {
    // if (this.contentLoading) {
    //   // this.setCacheType('')
    //   this.contentLoading = false
    // }
  }
}
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
    width: 100%;
  }

</style>



