import axios from 'axios'
import md5 from 'md5'
import cookie from 'js-cookie'

import { Loading, MessageBox } from 'element-ui'

// const baseURL = 'http://127.0.0.1:8900/'
const baseURL = '//' + window.location.host

let http = axios.create({
  baseURL: baseURL
})

const tempNonce = []

const tokenKey = 'token'
const refreshTokenKey = 'refreshToken'

http.interceptors.request.use(function (config) {
  if (config.method !== 'get') {
    const timestamp = Math.floor(new Date().getTime())
    const nonce = md5(JSON.stringify(config.data) + config.url + timestamp)
    config.headers.common['timestamp'] = timestamp
    config.headers.common['nonce'] = nonce
    if (tempNonce.indexOf(nonce) !== -1) {
      // eslint-disable-next-line
      return Promise.reject({
        data: {
          code: 809,
          url: config.url,
          data: config.data,
          msg: '重复请求'
        }
      })
    } else {
      tempNonce.push(nonce)
    }
  }

  config.headers.common['Authorization'] = cookie.get(tokenKey) || ''
  return config
})

// 添加响应拦截器
http.interceptors.response.use(function (response) {
  // if (tempNonce.indexOf(response.config.nonce) !== -1) {
  //   tempNonce.splice(tempNonce.indexOf(response.config.nonce), 1)
  // }
  // 对响应数据做点什么
  const data = response.data
  if (data) {
    if (data.code) {
      switch (data.code) {
        case 901:
        case 903:
          alert(data.msg)
          MessageBox.alert(data.msg + '，是否刷新登录信息？', '提示', {
            confirmButtonText: '确定',
            callback: () => {
              const loading = Loading.service({
                fullscreen: true,
                text: '正在请求...'
              })
              let config = {
                headers: {
                  common: {
                    timestamp: new Date().getTime(),
                    nonce: Math.random()
                  }
                }
              }
              http.put('/api/auth/refresh', 'refresh_token=' + cookie.get(refreshTokenKey), config).then(
                response => {
                  loading.close()
                  const backInfo = response.data
                  if (backInfo.flag) {
                    cookie.set(tokenKey, backInfo.data.token, 1 / 24)
                    cookie.set(refreshTokenKey, backInfo.data.refreshToken, 1)
                  }
                })
            }
          })
          break
        default:
          break
      }
      return data
    }
    if (data.flag === false) {
      // 非法请求和重复请求不提示信息
      if (data.code !== 802 && data.code !== 809) {
        alert(data.msg)
      } else {
      }
      return data
    }
  }
  return data
}, function (error) {
  console.error('axios error', error)
  if (!error.response) {
    return Promise.reject(error)
  }
  return Promise.reject(error.response.data, error.response)
})

export {http}



